* {
  margin: 0;
}

body{
  background-color: #f5f6fa;
}

.app_header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}

.app_left{
  flex: 0.9;
}

.app_stats {
  display: flex;
  justify-content: space-between;
}

.app {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.app__graph {
  flex-grow: 1;
}

.app__graphTitle {
  margin-top: 40px;
  margin-bottom: 10px;
}